import React, { useState } from 'react'

import 'photoswipe/dist/photoswipe.css'
import 'photoswipe/dist/default-skin/default-skin.css'
import { Gallery, Item } from 'react-photoswipe-gallery'

import { url } from '../redux/api/endpoints'


export function PhotoGallery (props) {
  const { products } = props

  return (
    products.length > 0 &&
      <Gallery>
        <Item
          original="https://placekitten.com/1024/768?image=1"
          thumbnail="https://placekitten.com/80/60?image=1"
          width="1024"
          height="768"
          title="Grapefruit slice atop a pile of other slices"
        >
          {({ ref, open }) => (
            <img ref={ref} onClick={open} src="https://placekitten.com/80/60?image=1" 
              className='rounded-m'
              />
          )}
        </Item>
        <Item
          original="https://placekitten.com/1024/768?image=2"
          thumbnail="https://placekitten.com/80/60?image=2"
          width="1024"
          height="768"
        >
          {({ ref, open }) => (
            <img ref={ref} onClick={open} src="https://placekitten.com/80/60?image=2" className='rounded-m ml-1'/>
          )}
        </Item>
        <Item
          original="https://placekitten.com/1024/768?image=2"
          thumbnail="https://placekitten.com/80/60?image=2"
          width="1024"
          height="768"
        >
          {({ ref, open }) => (
            <img ref={ref} onClick={open} src="https://placekitten.com/80/60?image=2" className='rounded-m ml-1'/>
          )}
        </Item>
        <Item
          original="https://placekitten.com/1024/768?image=2"
          thumbnail="https://placekitten.com/80/60?image=2"
          width="1024"
          height="768"
        >
          {({ ref, open }) => (
            <img ref={ref} onClick={open} src="https://placekitten.com/80/60?image=2" className='rounded-m ml-1'/>
          )}
        </Item>
      </Gallery>
)}

export function AllPhotoGallery (props) {
  const { products } = props

  return (
    products.length > 0 &&
      <Gallery fullscreenButton={false} shareButton={false}>
        <div className='d-flex flex-wrap' >
          {products.map(product => 
            product.images.length > 0 && product.images.map(img =>
              <Item
                original={url + img.catalog_image}
                thumbnail={url + img.thumb_image}
                width={img.cat_width}
                height={img.cat_height}
                title={
                  `
                    ${product.title} - ${product.price} \n
                    \n ${product.description && product.description}
                  ` 
                }
              >
                {({ ref, open }) => (
                  <img ref={ref} onClick={open} 
                    src={url + img.thumb_image} 
                    className='mx-1 my-1'
                    />
                )}
              </Item>
            )
          )}
        </div>
      </Gallery>
  )
}